.solutions {
    overflow: hidden;

    &__container {
        position: relative;
        z-index: 1;

        @include second-theme('solutions') {
            &__container {
                &::before {
                    background: url('../images/ico-plumb-4-th-2.png') no-repeat center / contain;
                }

                &::after {
                    background: url('../images/ico-plumb-5-th-2.png') no-repeat center / contain;
                }
            }
        }

        &::before {
            position: absolute;
            content: "";
            top: 50px;
            left: 0;
            display: block;
            width: 180px;
            height: 180px;
            background: url('../images/ico-plumb-4.png') no-repeat center / contain;
        }

        &::after {
            position: absolute;
            content: "";
            bottom: 40%;
            right: -60px;
            display: block;
            width: 300px;
            height: 240px;
            background: url('../images/ico-plumb-5.png') no-repeat center / contain;
        }

        @include for-tablet {
            &::before {
                background-position: top center;
            }

            &::after {
                bottom: -50px;
            }
        }
    }

    &__wrapper {
        position: relative;
        @include flex-wrap();
        z-index: 2;
    }

    &__card-sl {
        width: calc(calc(100% - 100px) / 3);
        margin-bottom: 50px;

        @include for-tablet {
            width: calc(50% - 16px);
            margin-bottom: 32px;
        }

        @include for-mobile {
            width: 100%;
            margin-bottom: 20px;
        }

        @include second-theme('solutions') {
            &__card-sl {
                &:nth-child(1) {
                    .card-sl__footer {
                        background: #49a6ff;
                    }
                }

                &:nth-child(2) {
                    .card-sl__footer {
                        background: #49a6ff;
                    }
                }

                &:nth-child(3) {
                    .card-sl__footer {
                        background: #00efcc;
                    }
                }

                &:nth-child(4) {
                    .card-sl__footer {
                        background: #00efcc;
                    }
                }

                &:nth-child(5) {
                    .card-sl__footer {
                        background: #49a6ff;
                    }
                }

                &:nth-child(6) {
                    .card-sl__footer {
                        background: linear-gradient(311deg, rgba(0, 239, 204, 0.6) 0%, rgba(73, 166, 255, 0.6) 36.09%, rgba(13, 240, 254, 0.6) 61.5%, rgba(0, 239, 204, 0.6) 100%);
                    }
                }
            }
        }

        &:nth-child(1) {
            .card-sl__footer {
                background: #009bef;
            }
        }

        &:nth-child(2) {
            .card-sl__footer {
                background: #009bef;
            }
        }

        &:nth-child(3) {
            .card-sl__footer {
                background: #ef009b;
            }
        }

        &:nth-child(4) {
            .card-sl__footer {
                background: #ef009b;
            }
        }

        &:nth-child(5) {
            .card-sl__footer {
                background: #009bef;
            }
        }

        &:nth-child(6) {
            .card-sl__footer {
                background: linear-gradient(311deg, rgba(239, 0, 155, 0.6) 0%, rgba(223, 143, 197, 0.6) 36.09%, rgba(0, 155, 239, 0.6) 61.5%, rgba(68, 162, 252, 0.6) 100%);
            }
        }
    }

    &__next-section {
        padding-top: 0;

        @include for-tablet {
            padding: 0;
            padding-top: 20px;
        }
    }
}